import {
  IonContent,
  IonHeader,
  IonLabel,
  IonList,
  IonListHeader,
  IonPage,
  IonToolbar,
  IonItem,
  IonButton,
  IonLoading,
  IonCheckbox,
  IonImg,
} from '@ionic/react';
import React, { useState, useEffect, useCallback, useRef } from 'react';
import './Connect.scss';
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from '../../store';
import { AppDispatch } from '../../store/store';
import { connect, disconnect, pair } from '../../store/reducers/bleReducer';
import { RouteComponentProps } from 'react-router';
import { CheckboxChangeEventDetail } from '@ionic/core';

import logoSvg from '../../assets/logo.svg';

const version = require('../../../package.json').version;

const Connect: React.FC<RouteComponentProps> = ({ history }) => {
  const [pairingMode, setPairingMode] = useState(false);
  const allDevices = useRef(false);
  const [cancel, setCancel] = useState(false);

  const state = useSelector((state: RootState) => ({
    connected: state.ble.connected,
    error: state.ble.error,
    connectAttempts: state.ble.connectAttempts,
    connecting: state.ble.connecting,
    hasKey: state.ble.authKey !== '',
  }));

  const dispatch = useDispatch<AppDispatch>();

  const pairBLE = async () => {
    setCancel(false);
    dispatch(pair(allDevices.current));
  };

  const connectBLE = useCallback(async () => {
    dispatch(connect(allDevices.current));
  }, []);

  useEffect(() => {
    if ((state.connectAttempts) > 0 && !state.connecting && !state.connected && !cancel) {
      new Promise(resolve => setTimeout(() => resolve(), 500)).then(() => {
        connectBLE();
      });
      return;
    }

    if (state.connected && !state.connecting) {
      history.push('/dashboard');
    }

  }, [state.connectAttempts, cancel, state.connected, state.connecting, history, connectBLE]);

  const pairingModeChange = (event: CustomEvent<CheckboxChangeEventDetail>) => {
    setPairingMode(event.detail.checked);
  };

  const allDevicesChange = (event: CustomEvent<CheckboxChangeEventDetail>) => {
    allDevices.current = event.detail.checked;
  };

  return (
    <IonPage className="connect">
      <IonLabel class="version-label">{version}</IonLabel>
      <IonHeader className="connect-header">
        <IonToolbar className="connect-header-toolbar">
          <IonImg className="connect-header-toolbar-logo" src={logoSvg} />
        </IonToolbar>
      </IonHeader>
      <IonContent className="connect-content">
        <IonList lines="none" className="connect-content-list">
          <IonListHeader className="connect-content-list-header">
            <IonLabel>Connect</IonLabel>
          </IonListHeader>
          <IonItem class="connect-content-list-spacer" />
          <IonItem className="connect-content-list-button" hidden={state.hasKey || state.connecting || state.connected}>
            <IonButton onClick={pairBLE} size="large">Pair</IonButton>
          </IonItem>
          <IonItem className="connect-content-list-button" hidden={state.connecting || state.connected || !state.hasKey}>
            <IonButton onClick={pairingMode ? pairBLE : connectBLE} size="large">
              {pairingMode ? 'Pair' : 'Connect'}
            </IonButton>
          </IonItem>
          <IonItem className="connect-content-list-options" hidden={state.connecting || state.connected || !state.hasKey}>
            <IonCheckbox
              defaultChecked={pairingMode}
              onIonChange={pairingModeChange}
              slot="start"
            />
            <IonLabel>Pair device again?</IonLabel>
          </IonItem>
          <IonItem className="connect-content-list-options" hidden={state.connecting || state.connected || !state.hasKey}>
            <IonCheckbox
              defaultChecked={allDevices.current}
              onIonChange={allDevicesChange}
              slot="start"
            />
            <IonLabel>Show all devices?</IonLabel>
          </IonItem>
          <IonItem class="connect-content-list-spacer" />
        </IonList>
      </IonContent>
      <IonLoading
        isOpen={(state.error !== undefined) || state.connecting}
        message={state.error || (state.connectAttempts > 1 ? `Retrying connection (${state.connectAttempts}) ...` : 'Connecting ...')}
        backdropDismiss={true}
        onDidDismiss={() => {
          setCancel(true);
          dispatch(disconnect());
        }}
      />
    </IonPage>
  );
};

export default Connect;
